<template>
  <a-modal v-bind="options" @cancel="handleCancel">
    <a-spin :spinning="loading">
      <p v-if="type === 2"> {{ nameArr.length > 3 ? `${nameArr.length}` : nameArr.join(',') }} </p>
      <a-form-model v-else-if="type === 1" ref="form" layout="vertical" :model="form">
        <a-form-model-item
          :label="` ${nameArr.length > 3 ? `${nameArr.length}` : nameArr.join(',')} `"
        >
          <radio-select v-model="form.flag" label-key="name" value-key="id" allow-clear :data="flagList"></radio-select>
        </a-form-model-item>

        <a-form-model-item v-if="form.flag === 0" label="">
          <a-textarea
            v-model.trim="form.desc"
            placeholder=""
            :auto-size="{ minRows: 3, maxRows: 5 }"
            :max-length="500"
            allow-clear
          />
        </a-form-model-item>
      </a-form-model>
    </a-spin>

    <template slot="footer">
      <a-button @click="handleCancel"></a-button>
      <a-button type="primary" :loading="confirmLoading" :disabled="btnDisabled" @click="handleOk"></a-button>
    </template>
  </a-modal>
</template>
<script>
// import { debounce } from '@/utils/util'
import newModel from '@/mixins/newModel'

export default {
  name: 'ApproveModel',
  mixins: [newModel],
  data() {
    return {
      flagList: [
        { id: 1, name: '' },
        { id: 0, name: '' }
      ],
      form: { flag: '', desc: '' },
      nameArr: []
    }
  },
  computed: {
    type() {
      // 1   2 
      return this.record.type
    },
    btnDisabled() {
      return this.type === 1 && (this.form.flag === '' || (this.form.flag === 0 && this.form.desc.length < 2))
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.nameArr = this.record.list.map(item => {
        return item.name
      })
    },
    handleOk() {
      this.submit()
    },

    async submit() {
      this.confirmLoading = true
      console.log(this.type === 1 ? '' : '', this.form)
      setTimeout(() => {
        this.$message.success('')
        this.$emit('on-submit-success')
        this.handleCancel()
      }, 2000)
      // 
      // try {
      //   // this.isEdit ? await this.apiEdit(params) : await this.apiAdd(params)
      //   this.confirmLoading = false
      //   this.$message.success('')
      //   this.$emit('on-submit-success')
      //   this.handleCancel()
      // } catch {
      //   this.confirmLoading = false // 
      // } finally {
      //   this.confirmLoading = false // loading
      // }
      // ---------------- async-------------------
      // insertOrUpdateDoctor(params)
      //   .then(() => {
      //     this.$message.success('')
      //     this.$emit('on-submit-success')
      //     this.handleCancel()
      //   })
      //   .finally(() => {
      //     this.confirmLoading = false
      //   })
    }
  }
}
</script>
