<template>
  <a-modal v-bind="options" @cancel="handleCancel">
    <a-spin :spinning="loading">
      <a-form-model ref="form" :model="form" :rules="rules" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-form-model-item label="" prop="stuId">
          <a-select
            v-if="isAdd"
            v-model="form.stuId"
            show-search
            allow-clear
            option-label-prop="label"
            :default-active-first-option="false"
            :dropdown-match-select-width="false"
            :filter-option="false"
            :not-found-content="null"
            @search="handleSearch"
            @change="handleChange"
          >
            <a-select-option v-for="_val in studentList" :key="_val.id" :label="_val.name" :value="_val.id">
              <ul class="drop-option">
                <li>{{ _val.name }}</li>
                <li>{{ _val.sex }}</li>
                <li>{{ _val.birth }}</li>
                <li>{{ _val.prefx }}-{{ _val.schoolYear }}-{{ _val.class }}</li>
              </ul>
            </a-select-option>
          </a-select>
          <span v-else>{{ form.name }}</span>
        </a-form-model-item>
        <a-form-model-item label="" prop="startTime">
          <span v-if="isView">{{ form.start }}</span>
          <a-date-picker
            v-else
            v-model="form.startTime"
            show-time
            value-format="YYYY-MM-DD HH"
            format="YYYY-MM-DD a"
            style="width: 100%;"
            placeholder=""
            @ok="startOkHandle"
          />
        </a-form-model-item>
        <a-form-model-item label="" prop="endTime">
          <span v-if="isView">{{ form.end }}</span>
          <a-date-picker
            v-else
            v-model="form.endTime"
            show-time
            value-format="YYYY-MM-DD HH"
            format="YYYY-MM-DD a"
            style="width: 100%;"
            placeholder=""
            @ok="endOkHandle"
          />
        </a-form-model-item>
        <a-form-model-item label="" prop="dateLength">
          <span>{{ form.dateLength }}</span>
        </a-form-model-item>
        <a-form-model-item label="" prop="leaveType">
          <span v-if="isView">{{ form.leaveType === '1' ? '' : '' }}</span>
          <radio-select
            v-else
            v-model="form.leaveType"
            label-key="name"
            value-key="id"
            allow-clear
            :data="leaveTypeList"
          />
        </a-form-model-item>

        <a-form-model-item v-if="form.leaveType === '1'" label="" prop="leaveReason">
          <span v-if="isView">{{ form.leaveReason }}</span>
          <a-textarea
            v-else
            v-model.trim="form.leaveReason"
            placeholder=""
            :auto-size="{ minRows: 3, maxRows: 5 }"
            :max-length="500"
            allow-clear
          />
        </a-form-model-item>
        <template v-else-if="form.leaveType === '2'">
          <a-form-model-item label="" prop="cause">
            <drop-selector v-model="form.cause" :disabled="isView" :data="causeList" placeholder="" />
          </a-form-model-item>
          <a-form-model-item label="" prop="symptom">
            <span v-if="isView">{{ form.symptom }}</span>
            <a-textarea
              v-else
              v-model.trim="form.symptom"
              placeholder=""
              :auto-size="{ minRows: 3, maxRows: 5 }"
              :max-length="500"
              allow-clear
            />
          </a-form-model-item>
        </template>
        <a-form-model-item label="">
          <!-- action="https://www.mocky.io/v2/5cc8019d300000980a055e76" -->
          <p v-if="isView && !fileList.length"></p>
          <a-upload
            v-else
            list-type="picture-card"
            :file-list="fileList"
            :before-upload="beforeUpload"
            :disabled="isView"
            @change="imageChange"
            @preview="handlePreview"
          >
            <div v-if="!isView && fileList.length < 8">
              <a-icon type="plus" />
              <div class="ant-upload-text">
                Upload
              </div>
            </div>
          </a-upload>
          <a-modal :visible="previewVisible" :footer="null" @cancel="previewCancel">
            <img alt="example" style="width: 100%" :src="previewImage" />
          </a-modal>
        </a-form-model-item>
      </a-form-model>
    </a-spin>

    <!--  -->
    <img-view
      v-if="imgViewOpts.imgVisible"
      ref="imgView"
      :img-visible.sync="imgViewOpts.imgVisible"
      :view-src="imgViewOpts.viewSrc"
      @viewEmit="mediaSwitch"
    ></img-view>
    <template #footer>
      <a-button v-if="!isView" @click="handleCancel"></a-button>
      <a-button type="primary" :loading="submitLoading" :disabled="loading" @click="handleOk">
        {{ isView ? '' : '' }}
      </a-button>
    </template>
  </a-modal>
</template>

<script>
import moment from 'moment'
import { debounce } from '@/utils/util'
import modal from '@/mixins/modal'
import { rqb, rqc, rqbc } from '@/utils/formRules'
import { editTask } from '_api/template'
import ImgView from '_com/FilePreview/ImgView' // 

const rules = {
  stuId: { ...rqbc, message: '' },
  startTime: { ...rqbc, message: '' },
  endTime: { ...rqbc, message: '' },
  leaveType: { ...rqc, message: '' },
  leaveReason: { ...rqb, message: '' },
  cause: { ...rqbc, message: '' },
  symptom: { ...rqb, message: '' }
}
const leaveTypeList = [
  { id: '1', name: '' },
  { id: '2', name: '' }
]
const causeList = [
  { id: '', name: '' },
  { id: '1', name: '' },
  { id: '2', name: '' },
  { id: '3', name: '' },
  { id: '4', name: '' },
  { id: '5', name: '' },
  { id: '6', name: '' },
  { id: '7', name: '' },
  { id: '8', name: '' },
  { id: '9', name: '' },
  { id: '10', name: '' },
  { id: '11', name: '' },
  { id: '12', name: '' },
  { id: '13', name: '' },
  { id: '14', name: '' },
  { id: '15', name: '' },
  { id: '16', name: '' }
]

function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = () => resolve(reader.result)
    reader.onerror = error => reject(error)
  })
}
const studentData = [
  {
    id: '1',
    name: '',
    sex: '',
    birth: '2001-12-12',
    prefx: '',
    schoolYear: '2019',
    class: '01'
  }
]
export default {
  name: 'AddLeaveModal',
  components: { ImgView },
  mixins: [modal],
  data() {
    this.leaveTypeList = leaveTypeList
    this.causeList = causeList
    this.studentData = studentData
    return {
      submitLoading: false,
      form: { stuId: undefined, dateLength: '0.5' },
      stuId: '',
      studentList: [],
      previewVisible: false,
      previewImage: '',
      fileList: [],
      picIndex: 0,
      imgViewOpts: {
        // 
        viewSrc: '',
        imgVisible: false
      }
    }
  },
  computed: {
    isAdd() {
      return this.record.status === 0
    },
    isEdit() {
      return this.record.status === 1
    },
    isView() {
      return this.record.status === 2
    },
    rules() {
      return this.isView ? {} : rules
    }
  },
  created() {
    this.validate = debounce(this.validate)
    this.studentList = this.studentData
    !this.isAdd && (this.form = this.record.info)
    if (this.isEdit) {
      this.form.startTime = this.dateInit(this.form.start)
      this.form.endTime = this.dateInit(this.form.end)
    }
    console.log(this.record)
  },
  methods: {
    dateInit(date) {
      let arr = date.split(' ')
      if (arr[1] === '') arr[1] = arr[1] === '' ? '9' : '14'
      return arr.join(' ')
    },
    startOkHandle(date) {
      this.form.start = moment(date).format('YYYY-MM-DD a')
    },
    endOkHandle(date) {
      this.form.end = moment(date).format('YYYY-MM-DD a')
    },
    handleSearch(value) {
      setTimeout(() => {
        this.studentList = this.studentData.map(_item => {
          if (_item.name.indexOf(value) > -1) {
            return _item
          }
        })
        console.log(this.studentList)
      }, 300)
    },
    handleChange(value) {
      console.log(value)
      // this.value = value
    },
    previewCancel() {
      this.previewVisible = false
    },
    async handlePreview(file) {
      if (!file.url && !file.preview) {
        file.preview = await getBase64(file.originFileObj)
      }
      this.imgViewOpts.imgVisible = true
      this.imgViewOpts.viewSrc = file.url || file.preview
      this.$nextTick(() => {
        this.$refs.imgView.init() // 
      })
    },
    imageChange({ fileList }) {
      this.fileList = fileList
      console.log(fileList)
    },
    beforeUpload(file) {
      console.log('aaa')
      this.fileList = [...this.fileList, file]
      return false
    },
    handleOk() {
      this.isView ? this.handleCancel() : this.validate()
    },
    validate() {
      this.$refs.form.validate(valid => {
        if (valid) {
          let params = {}
          this.submit(params)
        }
      })
    },
    submit(form) {
      this.submitLoading = true
      console.log('', this.form)
      setTimeout(() => {
        this.$message.success('')
        this.$emit('on-submit-success', this.form)
        this.handleCancel()
      }, 2000)

      // editTask(form)
      //   .then(() => {
      //     this.$message.success('')
      //     this.$emit('on-submit-success')
      //     this.handleCancel()
      //   })
      //   .finally(() => {
      //     this.submitLoading = false
      //   })
    },
    // 
    async mediaSwitch(isDown) {
      if (isDown) {
        this.picIndex = this.picIndex + 1 >= this.fileList.length ? 0 : this.picIndex + 1
      } else {
        this.picIndex = this.picIndex <= 0 ? this.fileList.length - 1 : this.picIndex - 1
      }
      let file = this.fileList.find((item, index) => index == this.picIndex)
      if (!file.url && !file.preview) {
        file.preview = await getBase64(file.originFileObj)
      }
      this.imgViewOpts.viewSrc = file.url || file.preview
      this.$nextTick(() => {
        this.$refs.imgView.init() // 
      })
    }
  }
}
</script>
<style lang="less" scoped>
.drop-option {
  display: flex;
  li {
    margin-right: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 50px;
    flex-grow: 2;
    &:first-child {
      flex-grow: 1;
      width: 80px;
    }
    &:nth-child(3),
    &:nth-child(4) {
      flex-grow: 1;
      width: 90px;
    }
  }
}
</style>
