<template>
  <div>
    <!--  -->
    <div class="header-cond">
      <a-card>
        <a-row :gutter="[16, 8]" type="flex" justify="space-between" align="middle">
          <a-col :span="4">
            <drop-selector
              v-if="canSchoolSelect"
              v-model="queryParam.shoolId"
              style="width:100%"
              :data="shoolList"
              placeholder=""
              @click="handleSearch"
            />
          </a-col>
          <a-col :span="14" class="cond-num">
            <div class="top-stat">
              <span> {{ approveData.pending | numberFormat }}</span>
              <span> {{ approveData.passed | numberFormat }}</span>
              <span> {{ approveData.rejected | numberFormat }}</span>
              <span> {{ approveData.revoked | numberFormat }}</span>
            </div>
          </a-col>
        </a-row>
      </a-card>
    </div>
    <a-card :bordered="false">
      <!--  -->
      <div class="table-page-search-wrapper">
        <a-form-model ref="form" :model="queryParam" layout="inline" @submit.native.prevent>
          <!--   -->
          <a-row :gutter="10">
            <a-col :md="4" :sm="24">
              <a-form-model-item label="" prop="id">
                <drop-selector
                  v-model="queryParam.prefx"
                  style="width:100%"
                  :data="schoolTypeList"
                  placeholder=""
                  @click="handleSearch"
                />
              </a-form-model-item>
            </a-col>

            <a-col :md="4" :sm="24">
              <a-form-model-item label="" prop="id">
                <drop-selector
                  v-model="queryParam.schoolYear"
                  style="width:100%"
                  :data="schoolYearList"
                  placeholder=""
                  @click="handleSearch"
                />
              </a-form-model-item>
            </a-col>

            <a-col :md="4" :sm="24">
              <a-form-model-item label="" prop="id">
                <drop-selector
                  v-model="queryParam.class"
                  style="width:100%"
                  :data="classList"
                  placeholder=""
                  @click="handleSearch"
                />
              </a-form-model-item>
            </a-col>
            <a-col :md="4" :sm="24">
              <a-form-model-item label="" prop="id">
                <drop-selector
                  v-model="queryParam.sex"
                  style="width:100%"
                  :data="sexList"
                  placeholder=""
                  @click="handleSearch"
                />
              </a-form-model-item>
            </a-col>
            <a-col :md="8" :sm="24">
              <a-form-model-item label="" prop="date">
                <a-range-picker v-model="queryParam.date" value-format="YYYY-MM-DD" style="width: 100%;" />
              </a-form-model-item>
            </a-col>
            <a-col :md="4" :sm="24">
              <a-form-model-item label="" prop="id">
                <drop-selector
                  v-model="queryParam.leaveType"
                  style="width:100%"
                  :data="leaveTypeList"
                  placeholder=""
                />
              </a-form-model-item>
            </a-col>

            <a-col :md="4" :sm="24">
              <a-form-model-item label="" prop="callNo">
                <a-input v-model.trim="queryParam.key" placeholder="" style="width: 100%;" />
              </a-form-model-item>
            </a-col>
            <!--   -->
            <!-- <template v-if="advanced"></template> -->

            <a-col :md="(!advanced && 8) || 24" :sm="24">
              <span
                class="table-page-search-submitButtons"
                :style="(advanced && { float: 'right', overflow: 'hidden' }) || {}"
              >
                <a-button ghost type="primary" @click="handleSearch"></a-button>
                <a-button style="margin-left: 8px;" @click="handleReset('form')"></a-button>
              </span>
            </a-col>
          </a-row>
        </a-form-model>
      </div>

      <!--     -->
      <div class="table-operator">
        <a-button type="primary" @click="handleClick({ key: '21' })"></a-button>
        <a-dropdown v-if="selectedRowKeys.length > 0">
          <a-menu slot="overlay" @click="handleClick">
            <a-menu-item key="11">
              <!-- <a-icon type="delete" /> -->
              
            </a-menu-item>
            <a-menu-item key="31">
              <!-- <a-icon type="delete" /> -->
              
            </a-menu-item>
          </a-menu>
          <a-button style="margin-left: 8px;">
            
            <a-icon type="down" />
          </a-button>
        </a-dropdown>
      </div>

      <div class="table-operator">
        <a-form-model ref="form1" :model="queryParam1" layout="inline" @submit.native.prevent>
          <a-row :gutter="10">
            <a-col :md="8" :sm="24">
              <a-form-model-item label="" prop="auditStatus">
                <checkbox-select
                  v-model="queryParam1.auditStatus"
                  allow-clear
                  :data="auditStatusList"
                  placeholder=""
                  @change="handleSearch"
                ></checkbox-select>
              </a-form-model-item>
            </a-col>
          </a-row>
        </a-form-model>
      </div>

      <!--  -->
      <s-table
        ref="table"
        size="default"
        row-key="id"
        :columns="columns"
        :data="loadData"
        :alert="false"
        show-pagination="auto"
        :row-selection="rowSelection"
        :scroll="{ x: 1500 }"
      >
        <template #serial="text, record, index">{{ index + 1 }}</template>
        <template #dateLength="text">{{ text }}</template>

        <template #status="text, record">
          <a-tag v-if="text === 0" :color="text | statusTypeFilter">{{ text | statusFilter }}</a-tag>
          <a-popover v-else placement="right">
            <template slot="content">
              <p>{{ record.approver }}</p>
              <p v-if="text === 3">{{ record.approvalTime }}</p>
              <template v-else>
                <p>{{ record.approvalTime }}</p>
                <p v-if="text === 2">{{ record.refuseReason }}</p>
              </template>
            </template>
            <a-tag style="cursor:pointer" :color="text | statusTypeFilter">{{ text | statusFilter }}</a-tag>
          </a-popover>
          <!-- <a-badge :status="text | statusTypeFilter" :text="text | statusFilter" /> -->
        </template>

        <template #applicant="text, record">
          <p>{{ text }}</p>
          <p>{{ record.applyPhone }}</p>
        </template>
        <!-- <template #description="text">
        <ellipsis :length="50" tooltip>{{ text }}</ellipsis>
      </template> -->

        <template #action="text, record">
          <a @click="handleClick({ key: '4', ...record })"></a>
          <a-divider type="vertical" />
          <a-dropdown v-if="record.status === 0">
            <a-menu slot="overlay" @click="({ key }) => handleClick({ key, ...record })">
              <a-menu-item key="1"></a-menu-item>
              <a-menu-item key="2"></a-menu-item>
              <a-menu-item key="3"></a-menu-item>
            </a-menu>
            <a>
              
              <a-icon type="down" />
            </a>
          </a-dropdown>
        </template>
      </s-table>

      <!--  -->
      <approve-modal
        v-if="approveShow"
        :visible.sync="approveShow"
        v-bind="approveInfo"
        @on-submit-success="handleSearch(false)"
      />
      <!--  -->
      <add-leave-modal
        v-if="addLeaveShow"
        :visible.sync="addLeaveShow"
        v-bind="addLeaveInfo"
        @on-submit-success="handleSearch(false)"
      />
    </a-card>
  </div>
</template>

<script>
import ApproveModal from './components/approve-modal'
import AddLeaveModal from './components/add-leave-modal'

import list from '@/mixins/list'

import { approveData } from './static' // 

const columns = [
  // {
  //   title: '#',
  //   scopedSlots: { customRender: 'serial' }
  // },
  {
    title: '',
    dataIndex: 'name'
  },
  {
    title: '',
    dataIndex: 'sex'
  },
  {
    title: '',
    dataIndex: 'prefx'
  },
  {
    title: '',
    dataIndex: 'schoolYear'
  },
  {
    title: '',
    dataIndex: 'class'
  },
  {
    title: '',
    dataIndex: 'start'
  },
  {
    title: '',
    dataIndex: 'end'
  },
  {
    title: '',
    dataIndex: 'dateLength',
    scopedSlots: { customRender: 'dateLength' }
  },
  {
    title: '',
    dataIndex: 'leaveType'
  },
  {
    title: '',
    dataIndex: 'leaveReason'
  },
  {
    title: '',
    dataIndex: 'status',
    scopedSlots: { customRender: 'status' }
  },
  {
    title: '',
    dataIndex: 'applicant',
    width: 120,
    scopedSlots: { customRender: 'applicant' }
  },
  {
    title: '',
    dataIndex: 'applyTime'
  },
  {
    title: '',
    dataIndex: 'action',
    width: 130,
    fixed: 'right',
    scopedSlots: { customRender: 'action' }
  }
]

const statusMap = {
  0: {
    status: 'orange',
    text: ''
  },
  1: {
    status: 'purple',
    text: ''
  },
  2: {
    status: 'red',
    text: ''
  },
  3: {
    status: '',
    text: ''
  }
}

const listData = [
  {
    id: '1',
    stuId: '1',
    name: '',
    sex: '',
    prefx: '',
    schoolYear: '2019',
    class: '01',
    start: '2020-7-7 ',
    end: '2020-7-7 ',
    leaveType: '1',
    dateLength: '0.5',
    leaveReason: 'ddddddddddddd',
    status: 0,
    applicant: '',
    applyPhone: '15236521478',
    applyTime: '2020-7-7 12:15',
    approvalTime: '2020-7-7 12:20',
    approver: '',
    refuseReason: ''
  },
  {
    id: '2',
    stuId: '2',
    name: '',
    sex: '',
    prefx: '',
    schoolYear: '2019',
    class: '01',
    start: '2020-7-7 ',
    end: '2020-7-7 ',
    leaveType: '2',
    dateLength: '0.5',
    leaveReason: 'ddddddddddddd',
    status: 1,
    applicant: '',
    applyPhone: '15236521478',
    applyTime: '2020-7-7 12:15',
    approvalTime: '2020-7-7 12:20',
    approver: '',
    refuseReason: 'aaaaaaaaa',
    cause: '1',
    causeName: '',
    symptom: 'mmmmmmmmmm'
  },
  {
    id: '3',
    stuId: '3',
    name: '',
    sex: '',
    prefx: '',
    schoolYear: '2019',
    class: '01',
    start: '2020-7-7 ',
    end: '2020-7-7 ',
    leaveType: '1',
    dateLength: '0.5',
    leaveReason: 'ddddddddddddd',
    status: 2,
    applicant: '',
    applyPhone: '15236521478',
    applyTime: '2020-7-7 12:15',
    approvalTime: '2020-7-7 12:20',
    approver: '',
    refuseReason: ''
  },
  {
    id: '4',
    stuId: '4',
    name: '',
    sex: '',
    prefx: '',
    schoolYear: '2019',
    class: '01',
    start: '2020-7-7 ',
    end: '2020-7-7 ',
    leaveType: '1',
    dateLength: '0.5',
    leaveReason: 'ddddddddddddd',
    status: 3,
    applicant: '',
    applyPhone: '15236521478',
    applyTime: '2020-7-7 12:15',
    approvalTime: '2020-7-7 12:20',
    approver: '',
    refuseReason: 'aaaaaaaaa'
  }
]

const auditStatusList = [
  { id: 1, name: '' },
  { id: 2, name: '' },
  { id: 3, name: '' },
  { id: 4, name: '' }
]

export default {
  name: 'TableList',
  components: {
    ApproveModal,
    AddLeaveModal
  },
  filters: {
    statusFilter(type) {
      return statusMap[type].text
    },
    statusTypeFilter(type) {
      return statusMap[type].status
    }
  },
  mixins: [list],
  data() {
    this.columns = columns
    this.auditStatusList = auditStatusList
    return {
      approveData: {
        pending: 35,
        passed: 71,
        rejected: 56,
        revoked: 22
      },
      shoolList: [
        { id: '1', name: '' },
        { id: '2', name: '' },
        { id: '3', name: '' },
        { id: '4', name: '' }
      ],
      schoolTypeList: [
        { id: '1', name: '' },
        { id: '2', name: '' },
        { id: '3', name: '' },
        { id: '4', name: '' },
        { id: '5', name: '' }
      ],
      schoolYearList: [
        { id: '2020', name: '2020' },
        { id: '2019', name: '2019' },
        { id: '2018', name: '2018' },
        { id: '2017', name: '2017' }
      ],
      classList: [
        { id: '1', name: '01' },
        { id: '2', name: '02' },
        { id: '3', name: '03' },
        { id: '4', name: '04' }
      ],
      sexList: [
        { id: '1', name: '' },
        { id: '2', name: '' }
      ],
      leaveTypeList: [
        { id: '1', name: '' },
        { id: '2', name: '' }
      ],
      // 
      queryParam: {
        shoolId: '1',
        prefx: undefined,
        schoolYear: undefined,
        class: undefined,
        sex: undefined,
        leaveType: undefined,
        date: ['', ''],
        key: ''
      },
      queryParam1: {
        auditStatus: [1, 2] // 
      },
      loadData: () => this.scrDataLoad(),

      approveShow: false, // 
      approveInfo: {
        loading: false,
        record: {}, // 
        settings: { title: '' } // 
      },
      addLeaveShow: false, // 
      addLeaveInfo: {
        loading: false,
        record: {}, // 
        settings: { title: '', width: 700 } // 
      }
    }
  },
  computed: {
    canSchoolSelect() {
      return this.orgProperty == 20 && [10, 20].includes(this.roleType) // 
    }
  },
  created() {
    // this.init()
  },
  methods: {
    async init() {
      const hide = this.$message.loading('...', 0)
      Promise.all([this.getStaticData()])
        .then(([apprRes]) => {
          this.approveData = apprRes
        })
        .finally(() => {
          hide()
        })
    },
    // table
    scrDataLoad() {
      return new Promise(resolve => {
        const data = {
          list: listData,
          pageNum: 1,
          pageSize: 10,
          total: '4',
          totalPage: '1'
        }
        resolve(data)
      })
    },
    getStaticData() {
      console.log(approveData)

      return new Promise(resolve => {
        setTimeout(() => {
          resolve(approveData)
        }, 2000)
      })
    },

    handleClick({ key, ...record }) {
      console.log(`Click on item ${key}`)
      switch (key) {
        case '1': // 
          this.toApprove([record])
          break
        case '11': // 
          this.toApprove(this.selectedRows)
          break
        case '3': // 
          this.toApprove([record], 2)
          break
        case '31': // 
          this.toApprove(this.selectedRows, 2)
          break
        case '21': // 
          this.toLeaveStudent({ status: 0 })
          break
        case '2': // 
          this.toLeaveStudent({ status: 1, info: record })
          break
        case '4': // 
          this.toLeaveStudent({ status: 2, info: record })
          break
      }
      // this.tfmVisible = true
    },
    toLeaveStudent(params) {
      console.log('ssss', params)
      this.addLeaveShow = true
      this.addLeaveInfo.record = params
    },
    toApprove(list, type = 1) {
      this.approveInfo.settings.title = type === 1 ? '' : ''
      this.approveShow = true
      this.approveInfo.record = { list, type }
    },
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRowKeys
      this.selectedRows = selectedRows
    }
  }
}
</script>

<style lang="less" scoped>
.header-cond {
  margin-bottom: 16px;
  .top-stat {
    font-size: 24px;
    text-align: right;
    font-weight: 600;
    span {
      padding-left: 20px;
    }
  }
}
</style>
<style lang="less">
.ant-popover-inner-content {
  max-width: 300px;
}
</style>
