import Vue from 'vue'

export default {
  props: {
    //  
    editAble: {
      type: Boolean,
      default: true
    },
    // 
    loading: {
      type: Boolean,
      default: false
    },
    // 
    record: {
      type: Object,
      default: null
    },
    // 
    settings: {
      type: Object,
      default: () => ({})
    }
  },
  computed: {
    options() {
      return {
        title: '',
        width: 500,
        centered: true,
        maskClosable: false,
        destroyOnClose: true, // 

        ...this.$attrs,
        ...this.settings
      }
    }
  },
  watch: {
    record: {
      handler(data) {
        let formData = Object.assign({}, this.form, data)
        //  
        // https://cn.vuejs.org/v2/api/#Vue-observable
        this.form = this.editAble ? Vue.observable(formData) : formData
      },
      deep: true,
      immediate: true
    }
  },
  data() {
    return {
      labelCol: { xs: { span: 24 }, sm: { span: 7 } },
      wrapperCol: { xs: { span: 24 }, sm: { span: 13 } }
    }
  },
  methods: {
    handleCancel() {
      this.$emit('update:visible', false)
      this.editAble && this.$refs.form?.resetFields() //  
    }
  }
}
